.piano-cell {
  border-right: 1px solid rgb(240, 240, 240);
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  min-height: 0px;
  font-size: 10px;
}

.piano-cell.clickable:hover {
  background-color: lightgray;
  border-color: lightgray;
  border-radius: 0;
}

.cell-trigger {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

/*
  Bass
*/

.cell-trigger-bass {
  border-radius: 0px;
  background-color: var(--bassDull);
}

.cell-trigger-bass.current {
  border-radius: 5px;
  background-color: var(--bassColor);
}

.cell-trigger-bass.playing {
  background-color: var(--bassLight);
}

/*
  Lenny
*/

.cell-trigger-lenny {
  border-radius: 0px;
  background-color: var(--lennyDull);
}

.cell-trigger-lenny.current {
  border-radius: 5px;
  background-color: var(--lennyColor);
}

.cell-trigger-lenny.playing {
  background-color: var(--lennyLight);
}

/*
  Drummer
*/

.cell-trigger-drummer {
  border-radius: 0px;
  background-color: var(--drummerDull);
}

.cell-trigger-drummer.current {
  border-radius: 5px;
  background-color: var(--drummerColor);
}

.cell-trigger-drummer.playing {
  background-color: var(--drummerLight);
}

.piano-roll {
  min-height: 0;
  min-width: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  margin: auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border: 6px solid lightgray;
  border-radius: 3px;
  box-sizing: border-box;
}

.piano-roll.bass {
  border-left: 6px solid var(--bassColor);
}

.piano-roll.lenny {
  border-right: 6px solid var(--lennyColor);
}

.piano-roll.drum-sequencer.drummer {
  border-bottom: 6px solid var(--drummerColor);
}

.piano-roll.drum-sequencer {
  flex-shrink: 1;
}

.piano-roll-editor {
  min-height: 0px;
  display: flex;
  flex-flow: column-reverse nowrap;
  justify-content: flex-end;
  flex-grow: 1;
}

.piano-roll-keys {
  min-height: 0px;
  width: 0px;
  display: flex;
  flex-flow: column-reverse nowrap;
  justify-content: flex-end;
}

.step-row {
  min-height: 0px;
  height: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

.piano-key {
  height: 100%;
  min-height: 0px;
  padding: 0px;
  ;
  margin: 0px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.piano-key p {
  width: 0px;
  font-size: 10px;
  float: left;
  /* display: */
  /* overflow-wrap: break-word; */
  /* word-wrap: break-word; */
  /* hyphens: auto; */
}

.piano-key.black {
  background-color: var(--black);
  color: var(--light-grey);
}

.piano-key.white {
  background-color: var(--light-grey);
  color: var(--black);
}
